import { FunctionComponent, useState } from 'react';
import { Job } from '../jobs/Patrol';
import { MovingAnchor } from '../space/Anchor';
import { Coordinate } from '../space/Coordinate';
import { Event, useEventListeners } from '../util/events';
const noop = () => {};
export class Entity {
	public readonly id: string;
	public readonly location: Coordinate;

	job?: Job;

	moveStart = new Event<[Coordinate]>();
	moveRest = new Event<[]>();

	constructor(id: string, location: Coordinate) {
		this.id = id;
		this.location = location;
	}

	play() {
		console.log('Entity#jobStart', this.job);
		return this.job?.start() || noop;
	}

	doJob(job: Job) {
		this.job = job;
		console.log('Entity has new job', this, job);
	}

	moveTo(coordinate: Coordinate) {
		console.log('Entity#moveTo', this, coordinate);
		this.moveStart.emit(coordinate);
	}

	public readonly Component: FunctionComponent = () => {
		return null;
	};
}

export const EntityComponent: FunctionComponent<{ entity: Entity }> = ({ entity }) => {
	const Component = entity.Component;
	const [position, animatePosition] = useState(entity.location);

	useEventListeners(
		() => [
			// Listen for the entity moveStart order;
			entity.moveStart.on(coordinate => animatePosition(coordinate))
		],
		[entity.moveStart]
	);

	return (
		<MovingAnchor moveTo={position} onRest={() => entity.moveRest.emit()}>
			<Component />
		</MovingAnchor>
	);
};
